/* CSS Reset */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: Arial, sans-serif;
    line-height: 1.6;
    background-color: #f8f8f8;
}

header {
    background: #333;
    color: #fff;
    padding: 8px 0;
}

nav {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    margin: auto;
}

.logo {
    margin-left: 9rem !important;
}

.tabs {
    margin-right: 9rem !important;
    margin-top: 10px !important;
}

img {
    height: 60px;
}

nav ul {
    list-style: none;
    display: flex;
}

nav ul li {
    margin-right: 20px;
}

nav ul li a {
    color: #fff;
    text-decoration: none;
    font-size: 16px;
}

.container {
    max-width: 960px;
    margin: auto;
    padding: 0 20px;
}

.me {
    text-align: justify;
    margin-top: 1rem;
}

.section {
    text-align: center;
}

.about {
    background-color: #f2f2f2;
}

.projects {
    background-color: #eaeaea;
    height: auto;
}

.lable {
    color: red!important;
}

.setting {
    margin: auto;
    height: auto;
    width: 90%;
}

.contact {
    background-color: #f7f7f7;
}

.project {
    margin-bottom: 20px;
}

form {
    display: flex;
    flex-direction: column;
}

input,
textarea,
button {
    margin-bottom: 10px;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
}

button {
    background-color: #333;
    color: #fff;
    border: none;
    cursor: pointer;
}

button:hover {
    background-color: #555;
}

footer {
    background: #333;
    color: #fff;
    text-align: center;
    padding: 4px 0;
}

section {
    padding: 3rem 0;
}

/* Media Queries */
@media (max-width: 1366px) {
    .logo, .tabs {
        margin: 0 1rem;
    }

    .setting {
        height: auto;
        width: 90%;
    }
}

@media (max-width: 1024px) {
    nav {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-around !important;
        padding-left: -10rem ;
    }

    .logo {
        margin: 1rem 0;
    }

    .tabs {
        margin: 1rem 0;
    }

    .setting {
        height: auto;
        width: 90%;
    }
}

@media (max-width: 767px) {

    .logo img {
        height: 40px;
        margin-left: -100px;
    }
    nav {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-around !important;
        padding-left: -10rem ;
    }
    .tabs ul {
        flex-direction: row;
        margin: auto;
        justify-content: center;
    }

    .tabs ul li {
        margin-bottom: 10px;
    }

    .setting {
        height: auto;
        width: 90%;
    }
}